    width: 1280px;
  }
}

.section-title {
  color: var(--title-text-color);
  text-align: center;
  letter-spacing: .03em;
  margin-bottom: 30px;
  font-size: 36px;
  font-weight: 900;
  line-height: 1.22;
}

