}

a, ul {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --font-family-main: Roboto, sans-serif;
  --primary-font-size: 12px;
  --primary-line-height: 1.17;
  --title-text-color: #212121;
  --primery-text-color: #757575;
  --accent-test-color: #fff;
  --accent-color: #2196f3;
  --hero-buton-hover: #188ce8;
  --primary-backgound-color: #fff;
  --main-backgound-color: #e5e5e5;
  --team-backgound-color: #f5f4fa;
  --footer-backgound-color: #2f303a;
  --heder-logo-color: #000;
  --adress-color: rgba(255, 255, 255, .6);
  --border-header-color: #ececec;
  --border-card-color: #eee;
  --hero-background-color: #c4c4c4;
  --icon-backgroun-color: rgba(255, 255, 255, .1);
  --icon-orignal-color: #afb1b8;
  --gallary-background-color: rgba(0, 0, 0, .25);
  --card-background-color: rgba(33, 150, 243, .9);
  --background-color: rgba(0, 0, 0, .2);
  --transform: 250ms cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.header {
