  padding: 10px 29px;
}

.button {
  color: var(--accent-test-color);
  background: var(--accent-color);
  transition: background-color var(--transform);
  cursor: pointer;
  border: 0;
  border-radius: 4px;
  font-family: inherit;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.button:hover, .button:focus {
  background-color: var(--hero-buton-hover);
}

.header-nav {
  margin-left: auto;
  margin-right: 15px;
  display: none;
}

@media screen and (min-width: 768px) {
  .header-nav {
    display: block;
  }
}

@media screen and (min-width: 1280px) {
  .header-nav {
    margin-right: 30px;
  }
}

.header-list {
  text-align: center;
  justify-content: center;
  display: flex;
}

.header-item:not(:first-child) {
  margin-left: 70px;
}

.header-link {
  letter-spacing: .02em;
  color: var(--title-text-color);
  transition: color var(--transform);
  padding-top: 32px;
  padding-bottom: 32px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  text-decoration: none;
  position: relative;
}

.header-link:hover, .header-link:focus {
  color: var(--accent-color);
  text-decoration: none;
}

.mobile-menu {
  width: 100%;
  height: 100%;
  background-color: var(--primary-backgound-color);
  z-index: 1000;
  transition: var(--transform);
  flex-direction: column;
  padding: 48px 15px 48px 40px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
  transform: translateY(100%);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu.is-open {
  transform: translateY(0);
}

.menu-button--close, .menu-button--open {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin: 0;
  padding: 0;
  display: inline-flex;
}

@media screen and (min-width: 768px) {
  .menu-button--close, .menu-button--open {
    display: none;
  }
}

.menu-button--close {
  z-index: 100;
  transition: fill var(--transform);
  position: absolute;
  top: 10px;
  right: 15px;
}

.menu-button--close:hover, .menu-button--close:focus {
  fill: var(--accent-color);
  cursor: pointer;
}

.menu-button--open {
  z-index: 500;
  transition: fill var(--transform);
  position: absolute;
  top: 5px;
  right: 15px;
}

.menu-button--open:hover, .menu-button--open:focus {
  fill: var(--accent-color);
  cursor: pointer;
}

.mobile-menu__nav--item:not(:last-child) {
  margin-bottom: 30px;
}

.mobile-menu__nav--link {
  letter-spacing: .02em;
  color: var(--title-text-color);
  font-size: 40px;
  font-weight: 500;
  line-height: 1.17;
  text-decoration: none;
}

.mobile-menu__nav--link.nav__link--active {
  color: var(--accent-color);
}

.mobile-menu__contacts {
  margin-top: auto;
  margin-bottom: 64px;
}

.mobile-menu__contacts--tel {
  letter-spacing: .02em;
  color: var(--accent-color);
  margin-bottom: 32px;
  font-size: 34px;
  font-weight: 500;
  line-height: 1.18;
  text-decoration: none;
  display: block;
}

@media screen and (max-width: 479px) {
  .mobile-menu__contacts--tel {
    font-size: calc(20px + 1vw);
  }
}

.mobile-menu__contacts--email {
  letter-spacing: .02em;
  color: var(--primery-text-color);
  font-size: 24px;
  font-weight: 500;
  line-height: 1.17;
  text-decoration: none;
}

.mobile-menu__socials {
  flex-wrap: wrap;
  display: flex;
}

.mobile-menu__socials--item {
  position: relative;
}

.mobile-menu__socials--item:not(:last-child) {
  margin-right: 20px;
}

.mobile-menu__socials--link {
  letter-spacing: .02em;
  color: var(--accent-color);
  font-size: 18px;
  font-weight: 500;
  line-height: 1.22;
  text-decoration: none;
}

.mobile-menu__socials--item:not(:last-child) .mobile-menu__socials--link:after {
  background-color: rgba(33, 33, 33, .2);
}

.mobile-menu__socials--link:after {
  content: " ";
  width: 1px;
  height: 22px;
  margin-left: 10px;
  position: absolute;
}

.engagement-container {
