  background: var(--card-background-color);
  transition: transform var(--transform);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.engagement-description:hover .engagement-overlay, .engagement-description:focus .engagement-overlay {
  transform: translateY(0);
}

.engagement-text {
  position: absolute;
  top: 0;
  left: 0;
}

.engagement-img {
